import { createStore } from 'vuex'
import axios from 'axios'
const header = {'headers':{'x-nxopen-api-key':process.env.VUE_APP_HEADER}}
const apiURL = process.env.VUE_APP_APIURL
export default createStore({
  state: {
    ocid:'',
    charStat:'',
    charInfo:{
      basic:{},
      stat:[
        {
          statSectionName:'',
          left:[],
          right:[]
        },
      ],
      hyperStat:{},
      ability:{},
      symbol:{},
    },
    selectedPage:{
      component:'statusPage',
      koreanName:'능력치',
    },
  },
  mutations: {
    setOCID(state, ocid){
      state.ocid = ocid
    },
    setCharInfoBasic(state, payload){
      state.charInfo.basic = payload
    },
    setCharInfoStat(state, payload){
      const sectionStats = [
        {
          statSectionName:'전투력',
          left:['전투력'],
          right:[]
        },
        {
          statSectionName:'능력치',
          left:['HP', 'STR', 'INT'],
          right:['MP', 'DEX', 'LUK']
        },
        {
          statSectionName:'세부 능력치',
          left:[
            "최소 스탯공격력",
            "최대 스탯공격력",
            "최종 데미지",
            "방어율 무시",
            "공격력",
            "마력",
            "재사용 대기시간 감소",
            "재사용 대기시간 미적용",
            "상태이상 추가 데미지"
          ],
          right:[
            "데미지",
            "보스 몬스터 데미지",
            "일반 몬스터 데미지",
            "크리티컬 확률",
            "크리티컬 데미지",
            "버프 지속시간",
            "속성 내성 무시",
            "소환수 지속시간 증가"
          ]
        },
        {
          statSectionName:'부가 능력치',
          left:[
            "메소 획득량",
            "아이템 드롭률",
            "추가 경험치 획득"
          ],
          right:[
            "스타포스",
            "아케인포스",
            "어센틱포스"
          ]
        }
          // {
          //   "stat_name": "상태이상 내성",
          //   "stat_value": "60"
          // },
          // {
          //   "stat_name": "스탠스",
          //   "stat_value": "100"
          // },
          // {
          //   "stat_name": "방어력",
          //   "stat_value": "7101"
          // },
          // {
          //   "stat_name": "이동속도",
          //   "stat_value": "156"
          // },
          // {
          //   "stat_name": "점프력",
          //   "stat_value": "118"
          // },
          // {
          //   "stat_name": "AP 배분 STR",
          //   "stat_value": "4"
          // },
          // {
          //   "stat_name": "AP 배분 DEX",
          //   "stat_value": "1123"
          // },
          // {
          //   "stat_name": "AP 배분 INT",
          //   "stat_value": "4"
          // },
          // {
          //   "stat_name": "AP 배분 LUK",
          //   "stat_value": "4"
          // },
          // {
          //   "stat_name": "AP 배분 HP",
          //   "stat_value": "0"
          // },
          // {
          //   "stat_name": "AP 배분 MP",
          //   "stat_value": "0"
          // },
          // {
          //   "stat_name": "버프 지속시간",
          //   "stat_value": "0"
          // },
          // {
          //   "stat_name": "공격 속도",
          //   "stat_value": "4"
          // },
          // {
          //   "stat_name": "무기 숙련도",
          //   "stat_value": "85"
          // },
      ]
      //payload 배열을 섹션별로 넣기 위해 키:밸류 형태로 바꿈 
      const resetPayloadIntoObject = () =>{
        const obj = {}
        for(const item of payload){
          obj[item.stat_name] = item.stat_value
        }
        return obj 
      }
      //퍼센트 표시
      const setPercentage = (obj, keyArr) =>{
        for(let i = 0; i<keyArr.length; i++){
          obj[keyArr[i]] = obj[keyArr[i]]+'%'
        }
      }
      //퍼센트단위가 필요한 목록
      const percentUnitNLists = [
        '아이템 드롭률', 
        '방어율 무시', 
        '최종 데미지', 
        '재사용 대기시간 미적용', 
        '상태이상 추가 데미지',
        '데미지',
        '보스 몬스터 데미지',
        '일반 몬스터 데미지',
        '버프 지속시간',
        '속성 내성 무시',
        '소환수 지속시간 증가',
        '메소 획득량',
        '추가 경험치 획득',
        '크리티컬 확률',
        '크리티컬 데미지'
      ]
      //왼쪽 배열, 오른쪽 배결 셋팅
      const createObjArray = (payloadObj, arr) =>{
        if(arr.length === 0){
          return []
        }
        const objArr = []
        for(let i = 0; i<arr.length; i++){
          const obj = {}
          obj.stat_name = arr[i]
          obj.stat_value = payloadObj[`${arr[i]}`]
          objArr[i] = obj
        }
        return objArr
      }
      //섹션별 나누기
      const sortBySection = (sectionIndex) => {
        state.charInfo.stat[sectionIndex] = {
          statSectionName:sectionStats[sectionIndex].statSectionName,
          left:createObjArray(payloadIntoObject, sectionStats[sectionIndex].left),
          right:createObjArray(payloadIntoObject, sectionStats[sectionIndex].right)
        }
      }
      const payloadIntoObject = resetPayloadIntoObject()

      setPercentage(payloadIntoObject, percentUnitNLists)
      payloadIntoObject['재사용 대기시간 감소'] = `${payloadIntoObject['재사용 대기시간 감소 (초)']}초 / ${payloadIntoObject['재사용 대기시간 감소 (%)']}%`
      for(let i = 0; i<Object.keys(sectionStats).length; i++){
        sortBySection(i)
      }
    },
    setCharInfoHyperStat(state, payload){
      state.charInfo.hyperStat = payload
    },
    setCharInfoAbility(state, payload){
      state.charInfo.ability = payload
    },
    setCharInfoSymbol(state, payload){
      state.charInfo.symbol = payload
    },
    setSelectedPage(state, page){
      state.selectedPage = page
    },

  },
  actions: {
    async getOCID({commit}){
      try{
        const ocid = await axios.get(`${apiURL}/id?character_name=쟤예스288`, header)
        commit('setOCID', ocid.data.ocid)
      }
      catch(e){
        console.log(e)
      }
    },
    async getCharInfo({commit}, payload){
      try{
        const params = `ocid=${payload.ocid}&date=${payload.date}`
        const response = await Promise.all([
          axios.get(`${apiURL}/character/basic?${params}`, header),
          axios.get(`${apiURL}/character/stat?${params}`, header),
          axios.get(`${apiURL}/character/hyper-stat?${params}`, header),
          axios.get(`${apiURL}/character/ability?${params}`, header),
          axios.get(`${apiURL}/character/symbol-equipment?${params}`, header),
        ])
        commit('setCharInfoBasic', response[0].data)
        commit('setCharInfoStat', response[1].data.final_stat)
        commit('setCharInfoHyperStat', response[2].data)
        commit('setCharInfoAbility', response[3].data)
        commit('setCharInfoSymbol', response[4].data)
      }
      catch(e){
        console.log(e)
      }

    }
  },
  modules: {

  },
  getters: {
    getOCID(state){
      return state.ocid
    },
    getCharStat(state){
      return state.charStat
    },
    getCharInfo(state){
      return state.charInfo
    },
    getSelectedPage(state){
      return state.selectedPage
    },
    
  },
})
